import React,{Component} from 'react';
import axios from 'axios';
import DressDisplay from './dressDisplay';
import './dress.css';

const durl="https://ajapparelapi.herokuapp.com/allproducts?widget=";

class DressLists extends Component{
    constructor(props){
        super(props)


        this.state={
            dressListing:''
        }
    }

    render(){
        return(
                <div>
                    <DressDisplay dressData={this.state.dressListing}/>
                </div>
        )
    }

    componentDidMount(){
    var dressId=this.props.match.params.id;
    sessionStorage.setItem('type',dressId);
    axios.get(`${durl}${dressId}`)
    .then((response) => {this.setState({dressListing:response.data})})
    }
}

export default DressLists;