import React,{Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import './dressDetails.css'


const Curl="https://ajapparelapi.herokuapp.com/product";
const Yurl = "https://ajapparelapi.herokuapp.com/addtocart";



class DressDetails extends Component{
    constructor(props){
        super(props)

        this.state={
            sizes:'',
            size:'',
            dress:{
                "brand":[
                   { "name":""}
                ],
                "size":[]
            },
            count:1
        
        }
    }


    handleSize = (event) =>{
        this.setState({sizes:event.target.value})
        console.log(event.target.value)
       
    }

    
    // onBack = (event) => {
    //      let dressId= sessionStorage.getItem('type')
    //      let url;
    //      if(event.target.value==""){
    //           url = `${Murl}/${dressId}`
    //           this.props.history.push(`/shop/women/${dressId}`)
    //       }else if(event.target.value==""){
    //         url =  `${Turl}/${dressId}`
    //         this.props.history.push(`/shop/men/${dressId}`)
    //     }else if(event.target.value==""){
    //         url= `${Burl}/${dressId}`
    //         this.props.history.push(`/ourbrands/${dressId}`)
    //     }else{
    //         this.props.history.push(`/list/${dressId}`)
    //     }
        
    // }

    addToCart = () =>{
        var cartData={
            "name": this.state.dress.name,
            "brand":this.state.dress.brand[0].name,
            "image": this.state.dress.thumb,
            "size": this.state.sizes,
            "price":this.state.dress.cost,
            "quantity":1
        }
        fetch(Yurl,{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(cartData)
        })
        this.incrementCount();
        this.sendAlert();
    }

    incrementCount = (event) =>{
        this.setState({count:this.state.count + 1})
        console.log(this.state.count)
    }

    sendAlert = () =>{
        alert('Item Added to Cart! Go to Cart to Place Order')
    }

     
    render(){
         var {dress} = this.state;
        return(
            <div className="row wrapper">
            <div className="col-xs-12 col-sm-12 col-md-6 col-lg-6 ">
                <Link to=""><img src={dress.thumb} className="responsive1" style={{height:"1200px", width:"782.55px"}}/></Link>
            </div>
            <div className="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div className="fixed">
                    <div className="header">{dress.brand[0].name}</div>
                    <div className="subheader">{this.state.dress.name}</div>
                    <hr/>
                    <div className="prices"><strong>Rs.{dress.cost}</strong></div>
                    <div className="size-box">
                        <p><strong>Select Size</strong></p>
                        {dress.size.map((item)=>
                            <option className="option" onClick={this.handleSize}>{item.name}</option>
                        )}
                    </div>
                    <div className="add-to-cart">
                        
                        <button className="b2" onClick={this.addToCart}>Add to Cart</button>
                    </div>
                </div>
            </div>
        </div>
        )
    }

    componentDidMount(){
        let dressid=this.props.match.params.id;
        axios.get(`${Curl}/${dressid}`)
        .then((response) => { this.setState({dress:response.data[0]})})

    }
   

    
}

export default DressDetails;



