import React from 'react';
import {Link} from 'react-router-dom';
import './dress.css';


const DressDisplay = (props) => {

    const renderDress = ({dressData}) => {
        if(dressData){
            return dressData.map((item) =>{
                return(
                
                  <div className="collection" id={item.id}>
                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-3">
                                <div id="piece">
                                    <div className="row">
                                        <Link to={`/dress/${item._id}`}> <img src={item.thumb} class="images" style={{height:"496.67px",width:"356.67px"}}/></Link>
                                    </div>
                                    <div id="data">
                                        <Link to={`/dress/${item._id}`}><div className="itemname">{item.name}</div></Link>
                                        <Link to={`/dress/${item._id}`}><div className="itemname price">Rs.{item.cost}</div></Link>
                                    </div>
                                </div>
                                <br/><br/>
                        </div>
                        
                  </div>
                 
                  
                 
                    
                )
            })
        }else{
            return(
                <div className="loader">
                <img src="/images/loader.gif"/>
                </div>
            )
           
        }
    }
    return(
            <div className="row">
                {renderDress(props)}
            </div>
            
    )
}

export default DressDisplay;