import React from 'react';
import {HashRouter, Route} from 'react-router-dom';
import Header from './Header';
import Home from './home/home';
import DressLists from './dressListing/dressList';
import DressDetails from './dressDetails/dressDetails';
import WomenTab from './navbarTabs/women/womenTabApi';
import MenTab from './navbarTabs/men/menTabApi';
import BrandApi from './navbarTabs/brands/brandApi';
import Cart from './cart/addToCartApi';
import Form from './order/form';
import OrderApi from './order/orderApi';
import Success from './order/success';



const Routing = () => {
    return(
        <HashRouter>
            <div>
                <Header/>
                <Route exact path="/" component={Home}></Route>
                <Route exact path="/list/:id" component={DressLists}></Route>
                 <Route exact path="/dress/:id" component={DressDetails}></Route> 
                <Route exact path="/shop/women/:id" component={WomenTab}></Route>
                <Route exact path="/shop/men/:id" component={MenTab}></Route> 
                <Route exact path="/ourbrands/:id" component={BrandApi}></Route> 
                <Route exact path="/viewcart" component={Cart}></Route> 
                <Route exact path="/order" component={Form}></Route>
                <Route exact path="/vieworder" component={OrderApi}></Route>  
                <Route exact path="/orderplaced" component={Success}></Route>  
             </div>
        </HashRouter>
    )
}

export default Routing;