import React,{Component} from 'react';
import MenTabDisplay from './menTabDisplay';
import axios from 'axios';

const Turl = "https://ajapparelapi.herokuapp.com/shopbytype/2?category=";

class MenTab extends Component{
    constructor(props){
        super(props)

        this.state={
            tab2:''
        }
    }

    render(){
        return(
            <div>
                <MenTabDisplay tab2Data={this.state.tab2}/>            
            </div>
        )
    }

    componentDidMount(){
        var dressId = this.props.match.params.id;
        sessionStorage.setItem('type',dressId)
        axios.get(`${Turl}${dressId}`)
        .then((response) => {this.setState({tab2:response.data})})
    }

}

export default MenTab;
