import React,{Component} from 'react';
import axios from 'axios';
import WomenTabDisplay from './womenTabDisplay';



const Murl = "https://ajapparelapi.herokuapp.com/shopbytype/1?category=";


class WomenTab extends Component{
    constructor(props){
        super(props)


        this.state = {
            tab1:''
        }
    }

    render(){
        return(
            <div>
                <WomenTabDisplay tab1Data={this.state.tab1}/>
            </div>
        )
    }

    componentDidMount(){
        var dressId = this.props.match.params.id;
        sessionStorage.setItem('type',dressId)
        axios.get(`${Murl}${dressId}`)
        .then((response)=>{this.setState({tab1:response.data})})
    }

}

export default WomenTab;