import React,{Component} from 'react';
import WidgetDisplay from './widgetDisplay';

const Wurl="https://ajapparelapi.herokuapp.com/widgets";

class Widget extends Component{
    constructor(){
        super()

        this.state={
            widgetList:''
        }
    }

    render(){
        return(
                <WidgetDisplay widgetData={this.state.widgetList}/>
        )
    }

    componentDidMount(){
        fetch(`${Wurl}`,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({widgetList:data})
        })
    }
}

export default Widget;
