import React,{Component} from 'react';
import axios from 'axios';
import BrandDisplay from './brandDisplay';


const Burl="https://ajapparelapi.herokuapp.com/dressBrands?brand="

class BrandApi extends Component{
    constructor(props){
        super(props)


        this.state={
            tab3:''
        }
    }


    render(){
        return(
            <div>
                <BrandDisplay tab3Data={this.state.tab3}/>
            </div>
        )
    }

    componentDidMount(){
        var dressId = this.props.match.params.id;
        sessionStorage.setItem('type',dressId);
        axios.get(`${Burl}${dressId}`)
        .then((response) => {this.setState({tab3:response.data})})

    }
}

export default BrandApi;