import React from 'react';
import {Link} from 'react-router-dom';
import './success.css';

const Success = () =>{
    return(
        <div className="success">
            <h1><center>Order Placed Successfully!!<i class="fa fa-smile-o" aria-hidden="true"></i></center></h1>
            <button><center><Link to="/vieworder" style={{color:"white"}}>Check my Orders</Link></center></button>
        </div>
        
    )
}

export default Success;