import React from 'react';
import {Link} from 'react-router-dom';
import './widgets.css';

const WidgetDisplay = (props) => {

const listWidgets = ({widgetData}) =>{
    if(widgetData){
        return widgetData.map((item)=>{
            return(
                <div className="container-fluid">
                    <div className="widget">
                        <Link to={`/list/${item.widget}`}>
                        <div className="center" style={{color:"#5c4bf5"}}>{item.name}</div>
                        <img src={`./images/${item.name}.jpg`} className="responsive2" height="1000px" width="100%"></img>
                       </Link>
                    </div>
                    
                </div>
            
            )
        })
    }
}

    return(
            <div>
                {listWidgets(props)}
            </div>
    )
}

export default WidgetDisplay;