import React from 'react';
import {Link} from 'react-router-dom';
import './cart.css';

const CartDisplay = (props) =>{
    
    const CartDetails = ({cartData}) =>{
        if(cartData){
            if(cartData.length>0){
                return cartData.map((item)=>{
                    return(
                        <React.Fragment>
                        <div className="cartdisplay">
                             <div className="cart" id={item._id}>
                                <div className="row">
                                    <div className="col-sm-6 col-md-3 col-lg-3">
                                        <img className="image" src={item.image} />
                                    </div>
                                    <div className="col-sm-6 col-md-9 col-lg-9">
                                        <div className="cart-name">
                                            <div className="brand">{item.brand}</div>
                                            <div className="name">{item.name}</div>
                                            <div className="size">Size: {item.size}</div>
                                            <div className="qty">Qty: 1</div>
                                            <div className="cost">Rs.{item.price}</div>
                                        </div>
                                        <div className="removeitem">
                                            <button onClick={() =>props.handleRemove(item._id)}>Remove Item</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        
                        </div>
                          
                       </React.Fragment>
                      
                    )
                })
            }else{
                return(
                    <div className="emptycart">
                        <h1><center>Your Cart is Empty!! <i class="fa fa-frown-o" aria-hidden="true"></i></center></h1>
                    </div>
                )
            }
            
        }
 

}
                        
// const total = product.reduce((prev, cur) => {
//     return prev + cur.quantity * cur.price;
//   }, 0);                      


    return(
      <div>
         <div className="heading">
            <h1>CART</h1>
        </div>
        <div className="bag">
            {CartDetails(props)}
            {/* <p>Total Amount: Rs.</p> */}
             <button className="placeorder"><Link to="/order"  style={{color:"white"}}>Place Order</Link></button>  
        </div>
        </div>
       
    )
}

export default CartDisplay;