import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import './form.css';

const Furl = "https://ajapparelapi.herokuapp.com/cart";
const Ourl = "https://ajapparelapi.herokuapp.com/placeorder";
const Eurl = "https://ajapparelapi.herokuapp.com/emptycart";

class Form extends Component{
    constructor(){
        super()

        this.state ={
            order_id:Math.floor(Math.random()*10000),
            clothes:'',
            username:'',
            phone:'',
            address:'',
            total:''
        }
    }

    handleChangeName = (event) => {
        this.setState({username:event.target.value})
    }
    handleChangePhone = (event) => {
        this.setState({phone:event.target.value})
    }
    handleChangeAddress = (event) =>{
        this.setState({address:event.target.value})
    }
    handleSubmit = () => {
        var data = {
            "_id":this.state.order_id,
            "username":this.state.username,
            "phone":this.state.phone,
            "address":this.state.address,
            "clothes":this.state.clothes
            // "brand":this.state.clothes.brand,
            // "itemname":this.state.clothes.name,
            // "price":this.state.clothes.price,
            // "size":this.state.clothes.size,
        }

        fetch(Ourl,{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data)
        })
        for(var i=0; i<=this.state.clothes.length; i++){
            this.handleCartAfterOrder();
        }
        
    }

        handleCartAfterOrder =() => {
            const requestOptions = {
                method: 'DELETE'
                };
                fetch(Eurl, requestOptions).then((response) => {
                return response;
                
                }).then((result) => {
                // do what you want with the response here
                return result;
                
                });
                console.log('Cart Emptyed')
        
            
        }

    

    render(){
        console.log(this.state.clothes)
        return(
            <div className="container">
                <div className="panel">
                    <div className="panel-heading">
                        <h4>Place Booking</h4>
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label className="control-label">Order_id</label>
                            <input type="text" name="order_id" readOnly className="form-control" value={this.state.order_id}/>
                        </div>
                        <div className="form-group">
                            <label className="control-label">User Name</label>
                            <input type="text" name="name"  className="form-control" value={this.state.name} onChange={this.handleChangeName}/>
                        </div>
                        <div className="form-group">
                            <label className="control-label">Phone</label>
                            <input type="text" name="phone"  className="form-control" value={this.state.phone} onChange={this.handleChangePhone}/>
                        </div>
                        <div className="form-group">
                            <label className="control-label">Address</label>
                            <input type="text" name="address"  className="form-control" value={this.state.address}
                            onChange={this.handleChangeAddress}/>
                        </div>
                        <button className="submitbtn" onClick={this.handleSubmit}>
                            <Link to="/orderplaced">Submit</Link>
                        </button>
                    </div>
                </div>
                
            </div>
        )
    }


    componentDidMount(){
        axios.get(`${Furl}`)
        .then((response) => {this.setState({clothes:response.data})})
    }



}

export default Form;